* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Raleway", sans-serif;
}

.App {
  width: 100%;
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  background: radial-gradient(
    47.39% 118.47% at 3.68% -21.35%,
    rgba(255, 219, 19, 0.1) 0%,
    rgba(255, 125, 75, 0.1) 30.4%,
    rgba(231, 130, 247, 0.1) 62.15%,
    rgba(216, 65, 137, 0.1) 100%
  );
  /* border-radius: 24px; */
}


.dashboard,
.proposal {
  background: #faf5f2;
  border: 6px solid rgba(39, 41, 49, 0.07);
  border-radius: 20px;
  max-width: 1180px;
  min-height: 850px;
}

@media screen and (min-width: 992px) {
  .dashboard,
  .proposal {
    margin: 30px 21px 20px 19px;
  }
}

@media screen and (max-width: 991px) and (min-width: 576px) {
  .dashboard,
  .proposal {
    background-color: transparent;
    background: #faf5f2;
    border: 6px solid rgba(39, 41, 49, 0.07);
    border-radius: 20px;
    max-width: 1180px;
    min-height: 850px;
  }
}

@media screen and (max-width: 575px) {
  .dashboard,
  .proposal {
    border: 4px solid rgba(39, 41, 49, 0.07);
    border-left-style: none;
    border-right-style: none;
    border-bottom-style: none;
    background: #faf5f2;
  }
}





